<script>
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  SelectValue,
  SelectTrigger,
  SelectItem,
  SelectContent,
  Select,
} from "@/components/ui/select";

export default {
  components: {
    Button,
    Input,
    SelectValue,
    SelectTrigger,
    SelectItem,
    SelectContent,
    Select,
  },
  data() {
    return {
      fullName: null,
      email: null,
      phone: null,
      salesVolume: null,
      salesVolumeOptions: [
        "50K AED - 100K AED",
        "100K AED - 200K AED",
        "200K AED - 300K AED",
        "300K AED - 400K AED",
        "400K AED - 500K AED",
        "Above 500K AED",
      ],
    };
  },
  mounted() {
    this.salesVolume = this.salesVolumeOptions[0];
  },
  methods: {
    async submitForm() {
      if (this.fullName && this.email && this.phone && this.salesVolume) {
        const resp = await this.$store.dispatch("joinAsAgent", {
          fullName: this.fullName,
          email: this.email,
          phone: this.phone,
          salesVolume: this.salesVolume,
          status: "DRAFT",
        });

        if (resp.message) {
          this.$store.commit("SET_TOASTER_MSG", {
            title: "✅ Success",
            description: resp.message,
          });
        }
      } else {
        this.$store.commit("SET_TOASTER_MSG", {
          title: "Please fill all the fields first.",
          variant: "destructive",
        });
      }
    },
  },
};
</script>

